@import "../../variables.modules";
@import url('https://fonts.googleapis.com/css?family=Roboto+Flex&display=swap');

.light {
  background-color: $white;
  color: $dark;
  transition: all 700ms;  // This is the same as the transition in src\Components\BaseLayout.module.scss 
} 


.figma{
  margin: auto;
  border-style: none;
  height: 100%;
  display: block;
  padding-left: 20em;
  align-items: center;
  padding-bottom: 2rem;
  

  @media only screen and (min-width: 1024px) and (max-width: 1199px){
    width: 60em;
    margin-left: -30%;
  }
  
  @media only screen and (min-width: 768px) and (max-width: 1023px){
    display:none;
  }
  
  @media only screen and (min-width: 480px) and (max-width: 767px){
    display:none;
  }
  
  @media only screen and (min-width: 0px) and (max-width: 479px){
    display:none;
  }
}

.pdf{
    text-align: center;
    font-size: 2.5rem;
    font-family: 'Titillium Web';
    padding-bottom: 2rem;
    a{
      color:rgb(221, 241, 250);
    }
}

.terminal {
  width: 80%;
  margin: 2rem auto 4rem auto;
  border-radius: .5rem;

  @media only screen and (min-width: 940px) {
    width: 50%;
    margin: 3rem auto 6rem auto;
  }
}

.topRow {
  background-color: #505050;
  color: white;
  padding: 0.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
  font-size: 1rem;

  @media only screen and (min-width: 940px) {
    font-size: 1.3rem;
  }

  i {
    padding-left: 0.5rem;
  }
}

.red {
  color: #FF6057;
}

.amber {
  color: #FFBD2E;
}

.green {
  color: #27C93F;
}

.window {
  padding: 1rem 2rem;
  border-radius: 0 0 .5rem .5rem;
  background-color: rgba(10, 4, 17, 0.596);
  color: $dark;

  @media only screen and (min-width: 940px) {
    padding: 2rem 3rem;
  }
  p, li {
    font-size: 6vw;
    font-family: 'Courier New', Courier, monospace;
    margin: 1.3rem 0;

    @media only screen and (min-width: 940px) {
      font-size: 1.8rem;
      margin: 2rem 0;
    }
  }
}


.emoji {
  margin-right: 1rem;
}

.gif{
  padding-left: 29rem;
}


.CaseStudy{
  @keyframes fadeInRight {
    from {
      opacity: 0;
      transform: translateX(-300px);
    }
    to {
      opacity: 1;
    }
  }
  
  h1{
    text-align: left;
    margin-left: 5%;
    font-size: 3rem;
    animation: fadeInRight 1s ease-in-out;
    font-family: 'Roboto', sans-serif;
    border-bottom:1px solid rgb(255, 255, 255);
    width: 80%; 
    padding-top: 5%;
  }
  
  img{
    padding-top: 4%;
    width: 60%;
    margin: auto;
    display: block;
    animation: fadeInRight 1s ease-in-out;
    @keyframes fadeInRight {
      from {
        opacity: 0;
        transform: translateX(-300px);
      }
      to {
        opacity: 1;
      }
    }
  }

  h2{
    margin-left: 10%;
    margin-top: 2%;
    font-size: 1.5rem;
    font-family: 'Roboto', sans-serif;
    width: 50%;
  }

  p{
    margin-left: 12%;
    margin-top: 1%;
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    width: 70%;
  }

  iframe{
    display: block;
    height: 25rem;
    margin:auto;
    width: 40%;
    margin-top: 2%;
  }
}

.Wireframes {
  
  img{
    width: 100%;
    margin: auto;
  }

  a{
    margin-left: 12%;
    margin-top: 1%;
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    width: 70%;
    color: #FFBD2E;
  }
}



